/* Header
   ========================================================================== */

   header {
    padding: clamp(8px, 1.5vw, 10rem) 0;
    .inner-header {
        max-height: $header-height;
        font-size: $logo-height;
        font-weight: 600;

        .logo {
            gap: 7px;
            color: $secondary-accent;
            font-size: $logo-height;
            font-weight: 600;

            .logo-path {
                color: $main-accent;
                font-size: 0em;
                display: block;
                height: $logo-height;
                width: $logo-width;
            }
            .separate_line {
                height: $logo-height + .5px;
                background: currentColor;
                width: 3px;
            }
            .version {
                background: $main-accent;
                color: $secondary-black;
                font-size: .6rem;
                padding: 5px .8rem;
                border-radius: 3px;
                line-height: 1.2;
                font-weight: 600;
            }
        }

        .right_wrapper {
            span {
                display: block;
                color: $main-accent;
                height: 24px;
                width: 24px;
            }
        }
    }
}


html.light header {
    .inner-header {
        .logo {
            color: $main-accent;
            [opacity] {
                color: $secondary-accent;
            }
            .version {
                background: $secondary-accent;
                color: $black;
                font-weight: 800;
            }
        }
        .right_wrapper {
            span a {
                color: $secondary-accent;
            }
            span a:hover {
                background-color: transparent;
            }
        }
    }
}